@import url('https://fonts.googleapis.com/css?family=Montserrat:700');

.App{
    position: relative;
    height: 100%;
    overflow: hidden;
    font-family: 'Montserrat', sans-serif;
}


.context {
    z-index: 2;
    
}

.navbar{
    padding:50px 10%;
    height: 25vh;
}

.home{
    height: 75vh;
}

#title{
    padding:30px 40px;
    border-radius: 58px;
    background: linear-gradient(145deg, #003aae, #0044cf);
    box-shadow:  41px 41px 82px #001a4d,
                 -41px -41px 82px #0066ff;
}

.event{
    height: fit-content;
    background: rgba(255, 255, 255, 0.06);
    border-radius: 16px;
    box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
    backdrop-filter: blur(18.2px);
    -webkit-backdrop-filter: blur(18.2px);
    border: 1px solid rgba(255, 255, 255, 0.17);
}

.rules{
    color: #001f5c;
}

